import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "@/components/ui/command";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Check, Search, ChevronDown } from "lucide-react";
import { cn } from "@/lib/utils";
import { ScannerList } from "../modal/ScannerListData";
import { useScannerSelect } from "../hooks/useScannerSelect";

const Scanner = () => {
  const {
    open,
    setOpen,
    selected,
    searchValue,
    setSearchValue,
    filteredScanners,
    handleSelect,
  } = useScannerSelect(ScannerList);

  return (
    <div className="space-y-4 w-full max-w-2xl mx-auto">
      {/* Searchable Dropdown */}
      <Popover open={open} onOpenChange={setOpen}>
        <PopoverTrigger asChild>
          <div className="relative w-full group">
            <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
              <Search className="h-5 w-5 text-gray-400 group-hover:text-gray-600 transition-colors" />
            </div>
            <button
              className="w-full flex items-center justify-between p-3 pl-10 border-2 border-gray-200 rounded-lg text-sm bg-white hover:border-blue-400 focus:border-blue-500 focus:ring-1 focus:ring-blue-200 transition-all shadow-sm"
              aria-expanded={open}
            >
              {selected ? (
                <div className="flex items-center gap-3">
                  <Avatar className="h-7 w-7 border-2 border-white shadow">
                    <AvatarImage src={selected.profile_picture} />
                    <AvatarFallback className="bg-blue-100 text-blue-600">
                      {selected.organization_name.charAt(0)}
                    </AvatarFallback>
                  </Avatar>
                  <span className="font-medium text-gray-800">
                    {selected.organization_name}
                  </span>
                </div>
              ) : (
                <span className="text-gray-500">Search organizations...</span>
              )}
              <ChevronDown
                className={cn(
                  "h-4 w-4 text-gray-400 transition-transform",
                  open ? "rotate-180" : ""
                )}
              />
            </button>
          </div>
        </PopoverTrigger>
        <PopoverContent
          className="w-[var(--radix-popover-trigger-width)] p-0 rounded-lg shadow-xl border border-gray-200"
          align="start"
          sideOffset={8}
        >
          <Command shouldFilter={false}>
            <div className="px-3 pt-3">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                <CommandInput
                  placeholder="Type to search..."
                  value={searchValue}
                  onValueChange={setSearchValue}
                  className="pl-10 border-b border-gray-100 rounded-none focus:ring-0 focus:border-blue-400"
                />
              </div>
            </div>
            <CommandList>
              <CommandEmpty className="py-6 text-center text-gray-500">
                No organizations found
              </CommandEmpty>
              <CommandGroup className="p-2">
                {filteredScanners.map((scanner) => (
                  <CommandItem
                    key={scanner.id}
                    value={scanner.id.toString()}
                    onSelect={() => handleSelect(scanner)}
                    className="p-0 m-0 w-full data-[selected=true]:bg-transparent rounded-md"
                  >
                    <div className="px-3 py-2 w-full">
                      <div
                        className={cn(
                          "flex items-center gap-3 w-full p-2 rounded-md",
                          "hover:bg-blue-50 transition-colors",
                          selected?.id === scanner.id ? "bg-blue-50" : ""
                        )}
                      >
                        <Avatar className="h-9 w-9 border-2 border-white shadow">
                          <AvatarImage src={scanner.profile_picture} />
                          <AvatarFallback className="bg-blue-100 text-blue-600">
                            {scanner.organization_name.charAt(0)}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex-1 min-w-0">
                          <h3 className="text-sm font-semibold text-gray-800 truncate">
                            {scanner.organization_name}
                          </h3>
                          <p className="text-xs text-gray-500 truncate">
                            {scanner.address}
                          </p>
                        </div>
                        <Check
                          className={cn(
                            "h-5 w-5 text-blue-500 flex-shrink-0",
                            selected?.id === scanner.id
                              ? "opacity-100"
                              : "opacity-0"
                          )}
                        />
                      </div>
                    </div>
                  </CommandItem>
                ))}
              </CommandGroup>
            </CommandList>
          </Command>
        </PopoverContent>
      </Popover>

      {/* Selected Organization Card */}
      {selected && (
        <div className="p-5 border border-gray-200 rounded-xl bg-white shadow-sm">
          <div className="flex items-start gap-4">
            <Avatar className="h-12 w-12 border-2 border-white shadow">
              <AvatarImage src={selected.profile_picture} />
              <AvatarFallback className="bg-blue-100 text-blue-600">
                {selected.organization_name.charAt(0)}
              </AvatarFallback>
            </Avatar>
            <div>
              <h3 className="text-lg font-bold text-gray-900">
                {selected.organization_name}
              </h3>
              <p className="text-sm text-gray-600 mt-1">{selected.address}</p>
              <div className="mt-3 flex gap-2">
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                  Active
                </span>
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                  Verified
                </span>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Scanner;
