import { useState } from "react";

interface Scanner {
  organization_name: string;
  address: string;
  profile_picture: string;
  id: number;
}

export const useScannerSelect = (scanners: Scanner[]) => {
  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState<Scanner | null>(null);
  const [searchValue, setSearchValue] = useState("");

  const filteredScanners = scanners.filter((scanner) =>
    scanner.organization_name.toLowerCase().includes(searchValue.toLowerCase())
  );

  const handleSelect = (scanner: Scanner) => {
    setSelected(scanner);
    setOpen(false);
    setSearchValue("");
  };

  return {
    open,
    setOpen,
    selected,
    searchValue,
    setSearchValue,
    filteredScanners,
    handleSelect,
  };
};
